.App {
  font-family: "MS PMincho";
  text-align: center;
}
p {
  font-family: "MS Gothic";
}
html,
body,
div,
span,
applet,
object,
h1,
video {
  margin: 0;
  padding: 0;
  border: 0;
}

.background {
  background-image: url("https://drive.google.com/uc?export=view&id=1YGpc4XFVvu-xzzf4fO5kn6jDspN6owTD");
  background-size: contain;
}

.back {
  background: rgba(255, 255, 255, 0.3);
}

