.title {
  padding: 25px;
  text-align: left;
}

h1 {
  /* font-size: 6vw; */
  font-size: 6vw;
  color: black;
}
.box1 {
  padding-bottom: 2%;
}
.box1 img {
  padding-top: 3%;
  padding-left: 30px;
}
.box1 .pick1 {
  padding-top: 10%;
}
.boxB {
  padding-top: 20px;
}
hr {
  border-top: 3px solid #bbb;
  border-bottom: 3px solid #fff;
  width: 95%;
}
a {
  text-decoration: none;
}
.pick1 h1:hover {
  color: #ff6a07d7;
}
h2:hover {
  color: #ff6a07d7;
}
a:visited {
  color: black;
}
.contents1 {
  text-align: left;
  padding-left: 5%;
}
.contents1 h1 {
  padding-top: 5%;
  padding-bottom: 20px;
  font-size: 4vw;
}
.contents1 img {
  padding-left: 25%;
}
ul {
  font-size: 3vw;
}
li {
  padding-bottom: 15px;
}
p {
  font-size: 2vw;
}
@media (max-width: 600px) {
  .box2 {
    width: 250px;
    padding-left: 25%;
  }

  .box3 {
    width: 250px;
    padding-left: 25%;
  }

  .box4 {
    width: 250px;
    padding-left: 25%;
  }

  .title img {
    width: 400px;
  }

  h1 {
    font-size: 4vw;
  }
  .box1 .pick1 {
    padding-top: 0%;
  }

  h2 {
    padding-top: 0%;
    margin-top: 0%;
  }

  hr {
    border-top: 0px solid #bbb;
    border-bottom: 0px solid #fff;
    width: 0%;
  }
}

@media (min-width: 600px) {
  .box2 {
    width: 50%;
    padding-left: 25%;
  }

  .box3 {
    width: 50%;
    padding-left: 25%;
  }

  .box4 {
    width: 50%;
    padding-left: 25%;
  }
  .box1:after {
    content: "";
    display: block;
    clear: both;
  }
  .box1 img {
    float: left;
  }
}

@media (min-width: 768px) {
  .boxA:after {
    content: "";
    display: block;
    clear: both;
  }

  .boxB {
    width: 100%;
  }

  .boxB:after {
    content: "";
    display: block;
    clear: both;
  }

  .box2 {
    float: left;
    width: 33.3%;
    padding-left: 0%;
  }

  .box3 {
    float: left;
    width: 33.3%;
    padding-left: 0%;
  }
  .box4 {
    float: left;
    width: 33.3%;
    padding-left: 0%;
  }
}
